import Vue from 'vue'
import Router from 'vue-router'

import routerLoading from './components/routerLoading'
import routerError from './components/routerError'

const profile = () => lazyLoadView(import('./views/profile'))
const helloWorld1 = () => lazyLoadView(import('./views/helloWorld1'))
const helloWorld2 = () => lazyLoadView(import('./views/helloWorld2'))
const helloWorld3 = () => lazyLoadView(import('./views/helloWorld3'))

// 
function lazyLoadView (asyncView) {
  const asyncComponent = () => ({
    component: asyncView, // 
    loading: routerLoading, // loading 
    error: routerError, // error 
    delay: 500, // loading 
    timeout: 3000 // 
  })
  return Promise.resolve({
    functional: true,
    render (h, { data, children }) {
      return h(asyncComponent, data, children)
    }
  })
}

Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      name: 'profile',
      component: profile
    },
    {
      path: '/helloWorld1',
      name: 'helloWorld1',
      component: helloWorld1
    },
    {
      path: '/helloWorld2',
      name: 'helloWorld2',
      component: helloWorld2
    },
    {
      path: '/helloWorld3',
      name: 'helloWorld3',
      component: helloWorld3
    }
  ]
})
