<template>
  <div class="router_loading">
    <div class="spinner">
      <div class="rect"></div>
      <div class="rect"></div>
      <div class="rect"></div>
      <div class="rect"></div>
      <div class="rect"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'routerLoading'
}
</script>

<style scoped lang="less">
  @loading-color: #5FE0FA;
  .router_loading {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 120;
    background: rgba(0,0,0,.4);
    .spinner {
      width: 50px;
      height: 60px;
      text-align: center;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-top: -30px;
      margin-left: -25px;
      .rect {
        background-color: @loading-color;
        width: 6px;
        height: 100%;
        margin: 0 2px;
        display: inline-block;
        animation: stretch 1.2s infinite ease-in-out;
      }
      .rect:nth-child(2) {
        animation-delay: -1.1s;
      }
      .rect:nth-child(3) {
        animation-delay: -1.0s;
      }
      .rect:nth-child(4) {
        animation-delay: -0.9s;
      }
      .rect:nth-child(5) {
        animation-delay: -0.8s;
      }
    }
  }
  .router_loading.hide {
    display: none;
  }
  @keyframes stretch {
    0%, 40%, 100% {
      transform: scaleY(0.4);
    }
    20% {
      transform: scaleY(1.0);
    }
  }
</style>
