<template>
  <div class="loading">
    <div class="base">
      <div class="cube"></div>
      <div class="cube"></div>
      <div class="cube"></div>
      <div class="cube"></div>
      <div class="cube"></div>
      <div class="cube"></div>
      <div class="cube"></div>
      <div class="cube"></div>
      <div class="cube"></div>
    </div>
    <div class="bar">
      <div class="progress" :style="{width: barWidth}"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'loading',
  props: ['progress'],
  data () {
    return {
      barWidth: '0'
    }
  },
  watch: {
    progress: function (newVal) {
      this.barWidth = newVal*100 + '%'
    }
  }
}
</script>

<style scoped lang="less">
  @bg-color: #eee;
  @loading-color1: #5FE0FA;
  @loading-color2: #1FA5C1;
  @loading-color3: #3EBCD8;
  @animation-time: 800ms;
  .loading {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 1000;
    overflow: hidden;
    .base {
      position: absolute;
      left: 50%;
      top: 50%;
      z-index: 1000;
      width: 15em;
      height: 15em;
      margin: -7.5em;
      padding: 3em;
      transform: rotateX(60deg) rotateZ(45deg);
      transform-style: preserve-3d;
      .cube, .cube:before, .cube:after {
        content: '';
        float: left;
        height: 3em;
        position: absolute;
        width: 3em;
      }
      .cube {
        background-color: @loading-color1;
        position: relative;
        transform: translateZ(3em);
        transform-style: preserve-3d;
        transition: .25s;
        box-shadow: 13em 13em 1.5em rgba(0, 0, 0, 0.1);
        animation: float 1s infinite;
      }
      .cube:before {
        background-color: @loading-color2;
        transform: rotateY(90deg) translateX(3em);
        transform-origin: 100% 0;
      }
      .cube:after {
        background-color: @loading-color3;
        transform: rotateX(-90deg) translateY(3em);
        transform-origin: 100% 100%;
      }
      .cube:nth-child(1) {
        animation-delay: 0.05s;
      }
      .cube:nth-child(2) {
        animation-delay: 0.1s;
      }
      .cube:nth-child(3) {
        animation-delay: 0.15s;
      }
      .cube:nth-child(4) {
        animation-delay: 0.2s;
      }
      .cube:nth-child(5) {
        animation-delay: 0.25s;
      }
      .cube:nth-child(6) {
        animation-delay: 0.3s;
      }
      .cube:nth-child(7) {
        animation-delay: 0.35s;
      }
      .cube:nth-child(8) {
        animation-delay: 0.4s;
      }
      .cube:nth-child(9) {
        animation-delay: 0.45s;
      }
    }
    .bar {
      position: absolute;
      left: 50%;
      top: 50%;
      z-index: 1000;
      width: 20em;
      height: 12px;
      margin-left: -10em;
      margin-top: 7em;
      border-radius: 5px;
      border: 1px solid @loading-color2;
      box-shadow:  0 7em 1.5em rgba(0, 0, 0, 0.1);
      overflow: hidden;
      .progress {
        background: @loading-color1;
        width: 30%;
        height: 100%;
        transition: all .5s;
      }
    }
  }
  .loading:before, .loading:after {
    content: '';
    display: inline-block;
    position: absolute;
    width: 50%;
    height: 100%;
    top: 0;
    z-index: 999;
    background-color: @bg-color;
    transition: all @animation-time;
  }
  .loading:before {
    left: 0;
  }
  .loading:after {
    right: 0;
  }
  @keyframes float {
    50% {
      transform: translateZ(.5em);
    }
  }
  .loading.loadingEnd:before {
    left: -50%;
  }
  .loading.loadingEnd:after {
    right: -50%;
  }
  .loading.loadingEnd>.base {
    display: none;
  }
  .loading.loadingEnd>.bar {
    display: none;
  }
  .loading.loadingHide {
    display: none;
  }
</style>
