<template>
  <div class="mainMenu" @click="changeMenu">
    <span class="menu_line"></span>
  </div>
</template>

<script>
export default {
  name: 'mainMenu',
  methods: {
    changeMenu () {
      this.$store.commit('changeRouters')
    }
  }
}
</script>

<style scoped lang="less">
  @sm-width: 768px;
  @text-color-pc: #eee;
  @text-color-phone: #333;
  @active-color: #258FB8;
  @animation-time: 500ms;
  .mainMenu {
    display: block;
    width: 38px;
    height: 38px;
    border-radius: 6px;
    cursor: pointer;
    position: fixed;
    z-index: 100;
    @media screen and (min-width: @sm-width) {
      border: 1px solid transparent;
      top: 40px;
      right: 3%;
    }
    @media screen and (max-width: @sm-width) {
      top: 4px;
      right: 20px;
    }
    .menu_line {
      display: inline-block;
      position: absolute;
      top: 16px;
      left: 5px;
      width: 26px;
      height: 4px;
      border-radius: 2px;
      background-color: @text-color-pc;
      transition: all @animation-time;
    }
    .menu_line:before, .menu_line:after {
      content: "";
      display: inline-block;
      position: absolute;
      left: 0;
      width: 26px;
      height: 4px;
      border-radius: 2px;
      background-color: @text-color-pc;
      transition: all @animation-time;
    }
    .menu_line:before {
      top: -8px
    }
    .menu_line:after {
      top: 8px
    }
  }
  .mainMenu:hover {
    background-color: @active-color;
  }
  .mainMenu.open>.menu_line {
    width: 0;
    background-color: @text-color-phone;
  }
  .mainMenu.open>.menu_line:before {
    transform: translateY(8px) rotate(45deg);
    background-color: @text-color-phone;
  }
  .mainMenu.open>.menu_line:after {
    transform: translateY(-8px) rotate(-45deg);
    background-color: @text-color-phone;
  }
</style>
