<template>
  <div class="app">
    <loading :class="{loadingEnd: isEnd, loadingHide: isHide}" :progress="loaded"></loading>
    <mainMenu :class="{open: menuShowing}"></mainMenu>
    <mainNav :isShowing="menuShowing"></mainNav>
    <keep-alive>
      <router-view></router-view>
    </keep-alive>
  </div>
</template>

<script>
import loading from './components/loading'
import mainMenu from './components/mainMenu'
import mainNav from './views/mainNav'

export default {
  components: {
    mainMenu,
    mainNav,
    loading
  },
  data () {
    return {
      loaded: 0,
      images: [
        './images/resume/4.jpg',
        './images/resume/css.png',
        './images/resume/html.png',
        './images/resume/javascript.png',
        './images/resume/php.png',
        './images/resume/vue.png',
        './images/resume/js-1.png',
        './images/resume/js-2.png',
        './images/resume/weChart.jpg',
        './images/resume/qq.jpg',
        './images/resume/bg-1.jpg',
        './images/resume/bg-2.jpg',
        './images/resume/bg-3.jpg',
        './images/resume/bg-4.jpg',
        './images/resume/bg-5.jpg'
      ],
      isHide: false,
      isEnd: false,
      loadingTime: 800
    }
  },
  computed: {
    menuShowing () {
      return this.$store.state.menuShowing
    }
  },
  mounted () {
    this.getProgress()// 
    document.onreadystatechange = this.hideLoading// loading
  },
  methods: {
    hideLoading () {
      if (document.readyState === 'complete') {
        let self = this
        this.isEnd = true
        setTimeout(function () {
          self.isHide = true
        }, this.loadingTime)
      }
    },
    getProgress () {
      let num = 1
      let totalNum = this.images.length
      let self = this
      for (let i = 0, img = []; i < totalNum; i++) {
        img[i] = new Image()
        img[i].src = this.images[i]
        img[i].onload = function () {
          num++
          self.loaded = (num / totalNum).toFixed(2)
        }
      }
    }
  }
}
</script>

<style lang="less">
  /**/
  @font-face {
    font-family: 'iconfont';
    src: url('./assets/font/iconfont.eot');
    src: url('./assets/font/iconfont.eot?#iefix') format('embedded-opentype'),
    url('./assets/font/iconfont.woff') format('woff'),
    url('./assets/font/iconfont.ttf') format('truetype'),
    url('./assets/font/iconfont.svg#iconfont') format('svg');
  }
  @font-face {
    font-family: 'imagination';
    src: url('./assets/font/imagination.ttf');
  }
  /**/
  html {
    width: 100%;
    height: 100%;
  }
  body {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    font-family: "Arial","Microsoft YaHei";
    font-size: 14px;
    color: #313131;
  }
  a {
    color: inherit;
    text-decoration: none;
  }
  ol, ul {
    list-style: none;
  }
  div,ol,ul,h1,h2,h3,h4,h5,h6,p,table {
    box-sizing: border-box;
  }
  .app {
    width: 100%;
    height:100%;
  }
</style>
