<template>
  <div class="nav_box" :class="{active: showMenu}">
    <div class="mainNav" :class="{active: showNav}">
      <ul class="nav_block" @click="changeRouter">
        <li class="nav_center">
          <div class="welcome_img" :style="{backgroundImage: images[0]}"></div>
          <p class="welcome_text">welcome !</p>
        </li>
        <li class="nav_item">
          <router-link class="link_image" to="/" :style="{backgroundImage: images[1]}"></router-link>
        </li>
        <li class="nav_item">
          <router-link class="link_image" to="/helloWorld1" :style="{backgroundImage: images[2]}"></router-link>
        </li>
        <li class="nav_item">
          <router-link class="link_image" to="/helloWorld2" :style="{backgroundImage: images[3]}"></router-link>
        </li>
        <li class="nav_item">
          <router-link class="link_image" to="/helloWorld3" :style="{backgroundImage: images[4]}"></router-link>
        </li>
      </ul>
    </div>
    <routerLoading :class="{hide: hideRouterLoading}"></routerLoading>
  </div>

</template>

<script>
import routerLoading from '../components/routerLoading'

export default {
  name: 'mainNav',
  components: {
    routerLoading
  },
  data () {
    return {
      images: [
        "url('./images/welcome.jpg')",
        "url('./images/resume.png')",
        "url('./images/hello.png')",
        "url('./images/resume.png')",
        "url('./images/resume.png')"
      ],
      loadImages: [
        './images/welcome.jpg',
        './images/resume.png',
        './images/hello.png',
        './images/resume.png',
        './images/resume.png'
      ],
      showMenu: false,
      showNav: false,
      hideRouterLoading: false
    }
  },
  props: ['isShowing'],
  watch: {
    isShowing (newVal) {
      this.showMenu = newVal
      if (newVal) {
        this.hideLoading()
      }
    }
  },
  methods: {
    changeRouter (e) {
      if (e.target.nodeName === 'A') {
        this.$store.commit('changeRouters')
      }
    },
    hideLoading () {
      let num = 0
      let totalNum = this.loadImages.length
      let self = this
      for (let i = 0, img = []; i < totalNum; i++) {
        img[i] = new Image()
        img[i].src = this.loadImages[i]
        img[i].onload = function () {
          num++
          if (num === totalNum) {
            self.hideRouterLoading = true
            self.showNav = true
          }
        }
      }
    }
  }
}
</script>

<style scoped lang="less">
  @sm-width: 768px;
  @text-color-phone: #333;
  @text-color-pc: #eee;
  @text-color: #fff;
  .nav_box {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 80;
    .mainNav {
      display: none;
      width: 100%;
      height: 100%;
      position: relative;
      background-color: @text-color-pc;
      .nav_block {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        margin: auto;
        padding: 0;
        @media screen and (min-width: @sm-width) {
          width: 85%;
          height: 85%;
        }
        @media screen and (max-width: @sm-width) {
          width: 100%;
          height: 100%;
        }
        .nav_item {
          box-sizing: border-box;
          position: relative;
          background-color: rgba(255,255,255,.5);
          display: inline-block;
          float: left;
          cursor: pointer;
          border: 8px solid @text-color-pc;
          @media screen and (min-width: @sm-width) {
            width: 50%;
            height: 50%;
          }
          @media screen and (max-width: @sm-width) {
            width: 100%;
            height: 20%;
          }
          .link_image {
            display: inline-block;
            width: 100%;
            height: 100%;
            background-position: center;
            background-size: cover;
            transition: all .5s;
            @media screen and (min-width: @sm-width) {
              filter: grayscale(100%);
            }
          }
        }
        .nav_center {
          box-sizing: border-box;
          border: 16px solid @text-color-pc;
          @media screen and (min-width: @sm-width) {
            position: absolute;
            top: 0;
            bottom: 0;
            right: 0;
            left: 0;
            z-index: 100;
            margin: auto;
            width: 200px;
            height: 200px;
            transform: rotate(45deg);
            overflow: hidden;
          }
          @media screen and (max-width: @sm-width) {
            width: 100%;
            height: 20%;
          }
          .welcome_img {
            @media screen and (min-width: @sm-width) {
              background-size: cover;
              background-position: center;
              width: 240px;
              height: 240px;
              position: relative;
              top: -36px;
              left: -36px;
              transform: rotate(-45deg);
            }
            @media screen and (max-width: @sm-width) {
              display: none;
            }
          }
          .welcome_text {
            @media screen and (min-width: @sm-width) {
              display: none;
            }
            @media screen and (max-width: @sm-width) {
              min-width: 220px;
              width: 100%;
              height: 100%;
              margin: 0;
              line-height: 100px;
              letter-spacing: 1px;
              text-align: center;
              font-size: 3em;
            }
          }
        }
      }
    }
  }

  .nav_box.active {
    display: block;
  }
  .nav_box>.mainNav.active {
    display: block;
  }
  .nav_box>.mainNav>.nav_block>.nav_item:hover .link_image {
    filter: none;
  }
</style>
